import React from 'react'
import { LogContext } from './Log'

import styles from './Input.module.css'

const propTypes = {}

class Input extends React.Component {
  attachRef = ref => {
    if (!ref) return
    ref.addEventListener('change', e =>
      this.add(`native(onChange): ${e.target.name} - ${e.target.value}`)
    )
    ref.addEventListener('blur', e =>
      this.add(`native(onBlur):  ${e.target.name}`)
    )
    ref.addEventListener('input', e =>
      this.add(`native(onInput):  ${e.target.name} - ${e.target.value}`)
    )
  }

  // handleInput = e => {
  //   const { onInput } = this.props

  //   this.add(`react(onInput): ${e.target.name} - ${e.target.value}`)
  //   onInput && onInput(e)
  // }
  handleBlur = e => {
    const { onBlur } = this.props

    this.add(`react(onBlur): ${e.target.name}`)
    onBlur && onBlur(e)
  }
  handleChange = e => {
    const { onChange } = this.props

    this.add(`react(onChange): ${e.target.name} - ${e.target.value}`)
    onChange && onChange(e)
  }

  handleAutofillDetect = e => {
    if (e.animationName.includes('autofill'))
      this.add(`autofill(start): ${e.target.name} - ${e.target.value}`)
  }

  render() {
    const { as: Component = 'input', detectAutofill, ...props } = this.props

    return (
      <LogContext.Consumer>
        {({ add }) => {
          this.add = add
          return (
            <Component
              autoCapitalize="off"
              {...props}
              onChange={this.handleChange}
              onBlur={this.handleBlur}
              ref={this.attachRef}
              className={detectAutofill ? styles.autofill : ''}
              onAnimationStart={this.handleAutofillDetect}
            />
          )
        }}
      </LogContext.Consumer>
    )
  }
}

Input.propTypes = propTypes

export default Input
