import React from 'react'
import Layout from 'react-tackle-box/Layout'

import Input from '../../components/Input'
import Log from '../../components/Log'

const propTypes = {}

class LoginPage extends React.Component {
  state = {
    detectAutofill: true,
  }

  handleSubmit = event => {
    let { username, password } = Array.from(event.target.elements).reduce(
      (acc, next) => {
        acc[next.name] = next
        return acc
      },
      {}
    )

    if (username.value !== 'someUser' || password.value !== 'somePass')
      event.preventDefault()
  }

  render() {
    return (
      <div className="container my-4">
        <ol>
          <li>
            Turn on the browser setting to save passwords
            <ul>
              <li>
                Chrome: go to chrome://settings/, show advanced options and
                check "Offer to save passwords I enter on the web"
              </li>
              <li>
                Firefox: go to Preferences/Security and check "Remember
                passwords for sites"
              </li>
              <li>
                Safari: go to Preferences / Passwords and check "AutoFill user
                names and passwords"
              </li>
            </ul>
          </li>
          <li>
            Enter username="someUser" / password="somePass" and hit submit
          </li>
          <li>
            There should be a popup that asks whether you want to store the
            password
          </li>
          <li>The next page should show the values in json format</li>
          <li>Go back</li>
        </ol>
        <label className="mt-4">
          <input
            type="checkbox"
            checked={this.state.detectAutofill}
            onChange={() =>
              this.setState({ detectAutofill: !this.state.detectAutofill })
            }
          />{' '}
          Try and detect autofill via webkit only hacks
        </label>
        <form
          method="POST"
          action="http://httpbin.org/post"
          onSubmit={this.handleSubmit}
        >
          <Layout direction="column" pad align="flex-start">
            <Layout pad align="center">
              <label htmlFor="user-field">User</label>
              <Input
                type="text"
                name="username"
                id="user-field"
                detectAutofill={this.state.detectAutofill}
              />
            </Layout>

            <Layout pad>
              <label htmlFor="password-field">Password</label>
              <Input
                type="password"
                name="password"
                id="password-field"
                detectAutofill={this.state.detectAutofill}
              />
            </Layout>
            <Layout pad justify="flex-start">
              <button>Submit</button>
              <button type="reset">Reset</button>
            </Layout>
          </Layout>
        </form>
        <Log />
      </div>
    )
  }
}

LoginPage.propTypes = propTypes

export default LoginPage
